@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

.bodyIntro {
    background: var(--black);
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.centerContainer {
    text-align: center;
}

.title {
    font-family: 'VT323', monospace; 
    font-size: 10rem; 
}

.text {
    font-family: 'VT323', monospace; 
    font-size: 5rem; 
    font-style: normal; 
    font-variant: normal; 
    font-weight: 700; 
    line-height: 125px;
    color: var(--main-white);
}

.continue {
    font-family: 'VT323', monospace; 
    font-size: 2rem; 
    font-style: normal; 
    font-variant: normal; 
    font-weight: 700; 
    line-height: 26.4px;
    text-decoration: none;
    color: var(--main-white);
    position:relative; 
    left:74%; 
    top:2px;
}

.continue:hover {
    color: var(--accent-color);
}