@import url(https://fonts.googleapis.com/css2?family=Actor&display=swap);
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
/* Google Font "Actor" */
/* This was the font that was used in the design, we could change it later */

/* Affects all elements of html */
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Actor", sans-serif;
}

/* CSS Varibles for color */
:root {
  --main-color: #56a5eb;
  --main-hover: #3a8ab380;
  --accent-color: #6989b9a1;
  --main-white: #ffffff;
  --accent-white: #f5f5f5;
  --black: #2b2b2b;
  --main-grey: #E5E5E5;
  --background-purple: #F7E9FF;
  --border-grey: #CACDD3;
  --grey-hover: #ddd;

}
/* Leave empty */
h3,
h4 {
    margin-bottom: 1rem;
    text-align: center;
}

h3 {
    font-size: 2.8rem;
    font-weight: 500;
}
h4{
    font-size: 1.6 rem;
    font-weight: 400;
    margin-bottom: 2rem;
}

body {
    background-image: url(/static/media/Background.404940c0.png);
    color: var(--black);
}
